"use client";

export default function MyEventsGrid({ events = [] }) {
  const hasEvents = Array.isArray(events) && events.length > 0;

  if (!hasEvents) {
    return (
      <div className="text-center py-5">
        <img
          src="/images/no-event-image.png"
          alt="No events"
          className="img-fluid mb-3"
          style={{ maxWidth: 300 }}
        />
        <h5 className="fw-bold">No events yet</h5>
        <p className="text-muted">
          Create your first event and unlock the full event-creator experience.
        </p>
      </div>
    );
  }

  return (
    <div className="row g-4">
      {events.map((e) => {
        const location = [e?.org?.city, e?.org?.state, e?.org?.country]
          .filter(Boolean)
          .join(", ");

        const date = e?.createdAt
          ? new Date(e.createdAt).toLocaleDateString()
          : "";

        return (
          <div key={e.id} className="col-xl-3 col-lg-4 col-md-6">
            <div className="card h-100 shadow-sm border-0 rounded-4">
              {/* IMAGE */}
              <div className="position-relative">
                <img
                  src={e.bannerImage || "/images/event-placeholder.png"}
                  alt={e.title || "Event"}
                  className="card-img-top rounded-top-4"
                  style={{ height: 160, objectFit: "cover" }}
                />

                {/* STATUS BADGE */}
                <span className="badge bg-primary position-absolute top-0 end-0 m-2">
                  {e.status}
                </span>
              </div>

              {/* BODY */}
              <div className="card-body">
                <h6 className="fw-bold text-truncate" title={e.title}>
                  {e.title || "Untitled Event"}
                </h6>

                <p className="text-muted small mb-1 text-truncate">
                  {location || "Location not specified"}
                </p>

                <p className="text-muted small mb-3">{date}</p>

                <div className="d-flex justify-content-between align-items-center">
                  <button className="btn btn-outline-primary btn-sm rounded-pill px-3">
                    Edit
                  </button>

                  <button className="btn btn-light btn-sm rounded-circle">
                    ⋮
                  </button>
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
}
