"use client";

import styles from "./MyEvents.module.css";

export default function MyEventsList({ events = [] }) {
  const hasEvents = Array.isArray(events) && events.length > 0;

  return (
    <div className={styles.wrapper}>
      {!hasEvents ? (
        /* ================= EMPTY STATE ================= */
        <div className={styles.emptyState}>
          <img
            src="/images/no-event-image.png"
            alt="No events"
            className={styles.emptyImage}
          />
          <h3 className={styles.emptyTitle}>No events yet</h3>
          <p className={styles.emptyText}>
            Create your first event and unlock the full event-creator experience.
          </p>
        </div>
      ) : (
        /* ================= EVENTS LIST ================= */
        <div className={styles.list}>
          {events.map((e) => (
            <div key={e.id} className={styles.listRow}>
              <img
                src={e.bannerImage || "/images/event-placeholder.png"}
                alt={e.title || "Event"}
                className={styles.thumbnail}
              />

              <div className={styles.listBody}>
                <h4 className={styles.listTitle}>
                  {e.title || "Untitled Event"}
                </h4>
                <p>
                  {e?.org?.city || "Location"} •{" "}
                  {e?.createdAt
                    ? new Date(e.createdAt).toLocaleDateString()
                    : ""}
                </p>
              </div>

              <span
                className={`${styles.status} ${
                  styles["status_" + e.status]
                }`}
              >
                {e.status}
              </span>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
